<template>
  <div>
    <el-button type="primary" @click="add">发布商品</el-button>
    <el-table :data="goodslist" height="410" border>
      <el-table-column prop="tg_id" label="商品ID" />
      <el-table-column prop="tg_title" label="商品名" />
      <el-table-column prop="tg_price" label="单价(元)" />
      <el-table-column prop="tg_createTime" label="添加时间" width="100" />
      <el-table-column label="操作">
        <div slot-scope="scope">
          <el-button title="添加服务" type="primary" size="mini" icon="iconfont icon-jiahao" @click="addserver(scope.row)" />
          <el-button title="修改" type="warning" size="mini" icon="iconfont icon-shenqing" @click="alert(scope.row)" />
        </div>
      </el-table-column>
    </el-table>
    <!-- 发布商品 -->
    <el-dialog style="margin-top: 2vh;" title="发布商品" :visible.sync="dialogVisible1" width="40%" :before-close="handleClose1">
      <div>
        <el-form label-width="70" :model="addform">
          <el-form-item label="商品名">
            <el-input name="tg_title" v-model="addform.tg_title"></el-input>
          </el-form-item>
          <el-form-item label="商品描述">
            <el-input type="textarea" name="tg_context" v-model="addform.tg_context"></el-input>
          </el-form-item>
          <el-form-item label="价格(元)">
            <el-input type="number" name="tg_price" v-model="addform.tg_price"></el-input>
          </el-form-item>
          <el-form-item label="商品图片">
            <el-upload
              ref="addformref"
              :action="root + 'goods/add'"
              name="file"
              multiple
              :data="addform"
              list-type="picture-card"
              :auto-upload="false"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose1">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改商品 -->
    <el-dialog title="修改商品" :visible.sync="dialogVisible2" width="40%" :before-close="handleClose2">
      <div>
        <el-form :model="alertform" label-width="70">
          <el-form-item label="商品名">
            <el-input v-model="alertform.tg_title"></el-input>
          </el-form-item>
          <el-form-item label="商品描述">
            <el-input v-model="alertform.tg_context" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label="商品价格(元)">
            <el-input v-model="alertform.tg_price" :min="0" type="number"></el-input>
          </el-form-item>
          <el-form-item label="商品图片">
            <el-col :span="24">
              <el-upload
                ref="alertformuploadref"
                :action="root + 'goods/addGoodsImg'"
                name="file"
                :file-list="alertform.tg_imgList"
                multiple
                :on-remove="removeimg"
                :data="{ tg_id: alertform.tg_id }"
                list-type="picture-card"
                :auto-upload="false"
              >
                <i class="el-icon-plus"></i>
              </el-upload>
            </el-col>
          </el-form-item>
          <el-form-item label="商品服务">
            <el-table :data="alertform.g_serverList" border>
              <el-table-column prop="ts_id" label="服务ID" />
              <el-table-column prop="ts_name" label="服务名" />
              <el-table-column prop="ts_price" label="单价(元)" />
              <el-table-column label="操作">
                <div slot-scope="scope">
                  <el-button title="修改" type="warning" size="mini" icon="iconfont icon-shenqing" @click="alertserver(scope.row)" />
                </div>
              </el-table-column>
            </el-table>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose2">取 消</el-button>
        <el-button type="primary" @click="submit">修 改</el-button>
      </span>
    </el-dialog>
    <!-- 添加服务 -->
    <el-dialog title="添加服务" :visible.sync="dialogVisible3" width="40%" :before-close="handleClose3">
      <div>
        <el-form :model="serverform" label-width="70">
          <el-form-item label="服务名称">
            <el-input v-model="serverform.ts_name"></el-input>
          </el-form-item>
          <el-form-item label="服务价格(元)">
            <el-input v-model="serverform.ts_price" :min="0" type="number"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose3">取 消</el-button>
        <el-button type="primary" @click="submit1">添 加</el-button>
      </span>
    </el-dialog>
    <!-- 修改服务 -->
    <el-dialog title="修改服务" :visible.sync="dialogVisible4" width="40%" :before-close="handleClose4">
      <div>
        <el-form :model="alertserverform" label-width="70">
          <el-form-item label="服务名称">
            <el-input v-model="alertserverform.ts_name"></el-input>
          </el-form-item>
          <el-form-item label="服务价格(元)">
            <el-input v-model="alertserverform.ts_price" :min="0" type="number"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose4">取 消</el-button>
        <el-button type="primary" @click="submit1">修 改</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      dialogVisible3: false,
      dialogVisible4: false,
      addform: {
        tu_id: JSON.parse(window.sessionStorage.user).u_id
      },
      alertform: {},
      serverform: {},
      alertserverform: {},
      goodsmsg: {},
      goodsid: 0,
      isadd: false,
      isserver: false,
      isimg: false,
      goodslist: [],
      id: JSON.parse(window.sessionStorage.user).u_id
    }
  },
  created() {
    this.getorderlist()
  },
  methods: {
    // 获取商品列表
    async getorderlist() {
      const { data: res } = await this.$http.get("goods/showGoodsByShopper", { params: { id: this.id } })
      console.log(res.obj)
      this.goodslist = res.obj
    },
    // 提交1
    async submit() {
      if (this.isadd) {
        this.$refs.addformref.submit()
      } else {
        const { tg_id, tg_title, tg_context, tg_price } = this.alertform
        const data = { tg_id, tg_title, tg_context, tg_price }
        const { data: res } = await this.$http.get("goods/updateGoods", { params: data })
        if (res.code != 200) return
        this.$refs.alertformuploadref.submit()
        console.log(res)
        this.getorderlist()
        this.handleClose2()
      }
    },
    // 提交2
    async submit1() {
      const { ts_id, tg_id, ts_name, ts_price } = this.alertserverform
      const data = this.isadd ? this.serverform : { ts_id, tg_id, ts_name, ts_price }
      const { data: res } = await this.$http.get(this.isadd ? "goods/addGoodsServer" : "goods/updateGoodsServer", { params: data })
      if (res.code !== 200) return
      this.getorderlist()
      this.isadd ? this.handleClose3() : this.handleClose4()
    },
    // 添加商品
    add() {
      this.dialogVisible1 = true
      this.isadd = true
    },
    // 添加服务
    addserver(row) {
      this.dialogVisible3 = true
      this.isadd = true
      this.serverform.tg_id = row.tg_id
    },
    // 修改商品
    async alert(row) {
      this.dialogVisible2 = true
      this.isadd = false
      this.goodsid = row.tg_id
      this.alertform = row
      this.alertform.tg_imgList.some(item => {
        item.url = this.root + item.ttgi_url
      })

      console.log(row)
    },
    // 修改服务
    async alertserver(row) {
      this.dialogVisible4 = true
      this.isadd = false
      this.alertserverform = row
      console.log(row)
    },

    // 移除图片
    removeimg(file, fileList) {
      console.log(123)
    },
    // 关闭对话框
    handleClose1() {
      this.dialogVisible1 = false
    },
    handleClose2() {
      this.dialogVisible2 = false
    },
    handleClose3() {
      this.dialogVisible3 = false
    },
    handleClose4() {
      this.dialogVisible4 = false
    }
  }
}
</script>
<style lang="less" scoped>
.el-upload {
  overflow: hidden;
}
</style>
