<template>
  <div>
    <h4>{{ user.u_nickname }}</h4>
    <p>用户真实姓名：{{ user.u_rname }}</p>
    <p>身份证号：{{ user.u_personID }}</p>
    <p>联系电话：{{ user.u_tel }}</p>
    <p>邮箱：{{ user.u_email }}</p>
    <p>性别：{{ user.u_sex === "1" ? "男" : "女" }}</p>
    <el-button type="primary" @click="dialogVisible = true">修改</el-button>

    <!-- 修改信息 -->
    <el-dialog title="修改信息" :visible.sync="dialogVisible" width="35%" :before-close="handleClose">
      <el-form label-width="70px" ref="userform" :model="user">
        <el-form-item label="用户昵称">
          <el-input v-model="user.u_nickname"></el-input>
        </el-form-item>
        <el-form-item label="	用户真实姓名">
          <el-input v-model="user.u_rname"></el-input>
        </el-form-item>
        <el-form-item label="身份证号">
          <el-input v-model="user.u_personID"></el-input>
        </el-form-item>
        <el-form-item label="联系电话">
          <el-input v-model="user.u_tel"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-select v-model="user.u_sex">
            <el-option value="1" label="男"></el-option>
            <el-option value="2" label="女"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      user: JSON.parse(window.sessionStorage.user),
      userform: {},
      dialogVisible: false
    }
  },
  methods: {
    updata() {},
    async submit(ref) {
      const { u_id, u_nickname, u_rname, u_sex, u_personID, u_tel } = this.user
      const data = {
        u_id,
        u_nickname,
        u_rname,
        u_sex,
        u_personID,
        u_tel
      }
      const { data: res } = await this.$http.get("user/userupdate", { params: data })
      if (res.code !== 200) return (this.user = JSON.parse(window.sessionStorage.user))
      window.sessionStorage.setItem("user", JSON.stringify(this.user))
      this.handleClose()
    },
    handleClose() {
      this.dialogVisible = false
    }
  }
}
</script>
<style lang="less" scoped></style>
