<template>
  <div>
    <el-table :data="orderlist" height="410" border>
      <el-table-column prop="to_id" label="订单ID" />
      <el-table-column prop="to_title" label="商品名" />
      <el-table-column prop="to_name" label="服务" />
      <el-table-column prop="customer.u_nickname" label="客户" />
      <el-table-column prop="to_price" label="总价(元)" />
      <el-table-column prop="" label="状态">
        <template slot-scope="scope">
          <p>{{ scope.row.to_status === 1 ? "下单" : scope.row.to_status === 2 ? "完成订单" : "退单" }}</p>
        </template>
      </el-table-column>
      <el-table-column prop="to_createTime" label="添加时间" width="100" />
      <el-table-column label="操作">
        <div slot-scope="scope">
          <el-button title="处理订单" type="primary" size="mini" icon="iconfont icon-dingdan" @click="order(scope.row)" />
        </div>
      </el-table-column>
    </el-table>
    <!-- 处理订单 -->
    <el-dialog title="处理订单" :visible.sync="dialogVisible1" width="40%" :before-close="handleClose1">
      <div>
        <el-form :model="serverform" label-width="70">
          <el-form-item label="订单ID">
            {{ serverform.to_id }}
          </el-form-item>
          <el-form-item label="商品">
            {{ serverform.to_title }}
          </el-form-item>
          <el-form-item label="客户" v-if="serverform.customer">
            {{ serverform.customer.u_nickname }}
          </el-form-item>
          <el-form-item label="状态">
            <el-select v-model="serverform.to_status">
              <el-option label="下单" :value="1"></el-option>
              <el-option label="完成订单" :value="2"></el-option>
              <el-option label="退单" :value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose1">取 消</el-button>
        <el-button type="primary" @click="submit">修 改</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible1: false,
      serverform: {},
      orderlist: [],
      id: JSON.parse(window.sessionStorage.user).u_id
    }
  },
  created() {
    this.getorderlist()
  },
  methods: {
    // 获取订单列表
    async getorderlist() {
      const { data: res } = await this.$http.get("goods/showOrderByShopper", { params: { id: this.id } })
      console.log(res.obj)
      this.orderlist = res.obj
    },
    // 提交
    async submit() {
      const { to_id, to_status } = this.serverform
      const data = { to_id, to_status }
      const { data: res } = await this.$http.get("goods/updateOrderByShopper", { params: data })
      if (res.code !== 200) return
      this.getorderlist()
      this.handleClose1()
    },
    // 处理订单
    order(row) {
      this.dialogVisible1 = true
      this.serverform = row
    },
    // 关闭对话框
    handleClose1() {
      this.dialogVisible1 = false
    }
  }
}
</script>
