<template>
  <div>
    <el-table :data="list" height="410" border>
      <el-table-column prop="saleDate" label="时间" />
      <el-table-column prop="num" label="完成订单数" />
      <el-table-column prop="total" label="营业额" />
    </el-table>
  </div>
</template>
<script>
import dateformat from "../../../config/dateformat"
export default {
  data() {
    return {
      id: JSON.parse(window.sessionStorage.user).u_id,
      list: []
    }
  },
  created() {
    this.getstatistics()
  },
  methods: {
    async getstatistics() {
      const { data: res } = await this.$http.get("goods/showSaleChart", { params: { id: this.id } })
      console.log(res)
      if (res.code !== 200) return
      this.list = res.obj
      this.list.some(item => {
        item.saleDate = dateformat.dateformat(item.saleDate)
      })
      console.log(this.list)
    }
  }
}
</script>
