<template>
  <div>
    <el-table :data="orderlist" height="410" border>
      <el-table-column prop="to_id" label="订单ID" />
      <el-table-column prop="to_title" label="商品名" />
      <el-table-column label="给予评分">
        <template slot-scope="scope">
          <el-rate v-if="scope.row.post_sale" v-model="scope.row.post_sale.tps_score" disabled :colors="colors"></el-rate>
          <p v-else>未评分</p>
        </template>
      </el-table-column>
      <el-table-column label="给予评价">
        <template slot-scope="scope">
          <p v-if="scope.row.post_sale">{{ scope.row.post_sale.tps_commment }}</p>
          <p v-else>未评价</p>
        </template>
      </el-table-column>
      <el-table-column prop="to_price" label="总价(元)" />
      <el-table-column prop="" label="状态">
        <template slot-scope="scope">
          <p>{{ scope.row.to_status === 1 ? "下单" : scope.row.to_status === 2 ? "完成订单" : "退单" }}</p>
        </template>
      </el-table-column>
      <el-table-column prop="to_createTime" label="添加时间" width="100" />
      <el-table-column label="操作">
        <div slot-scope="scope">
          <el-button
            v-if="scope.row.to_status === 2"
            title="申请服务"
            type="primary"
            size="mini"
            icon="iconfont icon-shenqing"
            @click="openserver(scope.row)"
          />
          <el-button
            v-if="scope.row.to_status === 2 && scope.row.tps_id === null"
            title="评价"
            type="primary"
            size="mini"
            icon="iconfont icon-pinglun1"
            @click="evaluate(scope.row)"
          />
        </div>
      </el-table-column>
    </el-table>
    <!-- 申请服务 -->
    <el-dialog title="申请服务" :visible.sync="dialogVisible1" width="35%" :before-close="handleClose1">
      <div class="server" v-if="goodsmsg">
        选择服务：
        <el-checkbox-group v-model="selectserver" @change="serverchange">
          <el-checkbox :label="item.ts_name" v-for="item in serverlist" :key="item.ts_id"
            >{{ item.ts_name }}&nbsp; <span class="prices">￥{{ item.ts_price }}</span>
          </el-checkbox>
        </el-checkbox-group>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose1">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 评价 -->
    <el-dialog title="评价" :visible.sync="dialogVisible2" width="35%" :before-close="handleClose2">
      <div>
        <el-form :model="pingjiaform">
          <el-form-item>
            <el-rate v-model="pingjiaform.tps_score" :colors="colors"></el-rate>
          </el-form-item>
          <el-form-item>
            <el-input v-model="pingjiaform.tps_commment" type="textarea"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose2">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      serverlist: [],
      selectserver: [],
      serverform: [],
      goodsmsg: {},
      ispingjia: false,
      pingjiaform: {},
      orderlist: [],
      id: JSON.parse(window.sessionStorage.user).u_id,
      colors: ["#99A9BF", "#F7BA2A", "#FF9900"]
    }
  },
  created() {
    this.getorderlist()
  },
  methods: {
    // 获取订单列表
    async getorderlist() {
      const { data: res } = await this.$http.get("user/showOrder", { params: { id: this.id } })
      console.log(res.obj)
      this.orderlist = res.obj
    },
    // 提交
    async submit() {
      if (!this.ispingjia) {
        this.serverlist.some((item, i) => {
          this.selectserver.some((item1, i1) => {
            if (item1 === item.ts_name) {
              this.serverform.to_price += parseInt(item.ts_price)
            }
          })
        })
      }
      let data = this.ispingjia ? this.pingjiaform : this.serverform
      const { data: res } = await this.$http.get(this.ispingjia ? "user/addPostSaleComment" : "user/updateOrderByCustomer", { params: data })
      if (res.code !== 200) return
      this.ispingjia ? this.handleClose1() : this.handleClose2()
    },
    // 服务
    async openserver(row) {
      this.dialogVisible1 = true
      this.ispingjia = false
      const { data: res } = await this.$http.get("user/showOneGoodsServer", { params: { id: row.tg_id } })
      this.serverlist = res.obj
      let { tu_id, to_id, to_price, to_title, to_name } = row
      this.selectserver = to_name.split(",")
      this.serverlist.some(item => {
        this.selectserver.some(item1 => {
          if (item1 === item.ts_name) {
            to_price = parseInt(to_price) - parseInt(item.ts_price)
          }
        })
      })
      this.serverform = { tu_id, to_id, to_price, to_title, to_name }
      console.log(this.serverform)
    },
    // 评价
    evaluate(row) {
      this.dialogVisible2 = true
      this.ispingjia = true
      const { tu_id, to_id } = row
      this.pingjiaform = { tu_id, to_id }
    },
    serverchange(val) {
      console.log(val)
    },
    // // 退款
    // async tuikuan(row) {
    //   // 弹窗询问是否申请退款
    //   const confirmres = await this.$confirm("是否申请退款?", "申请退款", {
    //     confirmButtonText: "是",
    //     cancelButtonText: "否",
    //     type: "warning"
    //   }).catch(err => err)
    //   if (confirmres != "confirm") {
    //     return this.$message.info("已取消申请")
    //   }
    // },
    // 关闭对话框
    handleClose1() {
      this.dialogVisible1 = false
    },
    handleClose2() {
      this.dialogVisible2 = false
    }
  }
}
</script>
